export async function submitPurchaseOrder(productionId, supplierName, itemName,
  quantityWeight, quantityVolume, unitPrice, totalPrice, username) {
  return apiClient
    .post('/purchase-order/create', {
        productionId,
        supplierName,
        itemName,
        quantityWeight,
        quantityVolume,
        unitPrice,
        totalPrice,
        createdBy: username,
    })
    .then(response => {
      if (response) {
        console.log(response.data);
        return response.data;
      }
      return false;
    })
    .catch(err => console.log(err))
}

